import React, { Component } from 'react';
import { addLeadingZero, numeral, COLORS } from '../../Shared';
import { DEFAULT_PRICE } from '../../utils/config';
import './Date.scss';

class Date extends Component {
  render() {
    return (
      <div
        className={this.props.selectedDay === this.props.date ? 'date active' : 'date'}
        onClick={() => {
          this.props.selectDay(this.props.date);
        }}
      >
        <div
          className={`borderTop ${this.props.selectedDay === this.props.date && 'active'}`}
          style={{
            borderTop:
              this.props.info.color > 0 ? `3px solid ${COLORS[this.props.info.color]}` : '',
          }}
        />
        <div className={'day'}>{addLeadingZero(this.props.date)}</div>
        <div className={'price'}>
          {numeral(this.props.info.price).format('0,0.[0000]')}{' '}
          <img
            src={`${process.env.PUBLIC_URL}/static/images/eth${
              this.props.selectedDay === this.props.date ? 'White' : ''
            }.png`}
            alt={'eth'}
          />
        </div>
        <div
          className={'name'}
          style={{
            fontWeight: this.props.info.color > 0 ? 600 : 400,
            color: this.props.info.color > 0 ? COLORS[this.props.info.color] : '#666666',
          }}
        >
          {this.props.info.name}
        </div>
      </div>
    );
  }
}

export default Date;
