import React, { Component, Fragment } from 'react';
import Date from './Date.js';
import DayDetail from './DayDetail';
import './Month.scss';
import { CALENDAR_CONFIG } from '../../utils/config';

class Month extends Component {
  constructor() {
    super();
    this.state = {
      selectedMonth: 0,
      selectedDay: 0,
      dayDetailTransform: `translateX(300px)`,
    };
  }

  componentDidUpdate = (prevProps) => {
    if (this.props.month !== prevProps.month) {
      this.setState({
        selectedMonth: 0,
        selectedDay: 0,
        dayDetailTransform: `translateX(300px)`,
      });
    }
  };

  selectDay = (day) => {
    this.setState((prevState) => ({
      selectedDay: prevState.selectedDay === day ? 0 : day,
      dayDetailTransform:
        prevState.dayDetailTransform === 'translateX(0)' && prevState.selectedDay === day
          ? `translateX(300px)`
          : 'translateX(0)',
    }));
  };

  closeDetail = () => {
    this.setState({ dayDetailTransform: `translateX(300px)`, selectedDay: 0 });
  };

  getMonth = () => {
    const dates = [];
    for (let i = 0; i < this.props.data.max; i++) {
      dates.push(
        <Date
          key={`${this.props.month}${i}`}
          month={this.props.month}
          date={i + 1}
          info={this.props.datesInfo[i]}
          selectDay={this.selectDay}
          selectedDay={this.state.selectedDay}
        />,
      );
    }
    return dates;
  };
  render() {
    return (
      <Fragment>
        <div className="month" align={'left'}>
          {this.getMonth()}
        </div>
        <DayDetail
          getContractData={this.props.getContractData}
          myNativeBalance={this.props.myNativeBalance}
          transform={this.state.dayDetailTransform}
          month={this.props.month}
          date={this.state.selectedDay}
          info={this.props.datesInfo[Math.max(this.state.selectedDay - 1, 0)]}
          closeDetail={this.closeDetail}
          address={this.props.address}
        />
      </Fragment>
    );
  }
}

export default Month;
