export const reduceMonth = (month) => {
  return month === 8
    ? CALENDAR_CONFIG[month].month.substring(0, 4)
    : CALENDAR_CONFIG[month].month.substring(0, 3);
};

export const reduceAddress = (addr) => {
  if (addr) {
    return addr.substring(0, 6) + '...' + addr.substring(addr.length - 4, addr.length);
  }
};

export const date2CountdownString = (date) => {
  let n = date.toUTCString();
  let timeString =
    n.substring(0, 3) +
    n.substring(7, 12) +
    n.substring(5, 7) +
    n.substring(11, n.length - 4) +
    ' UTC';
  return timeString;
};

export function sleep(time) {
  return new Promise((resolve) => setTimeout(resolve, time * 1000));
}

export const RELEASE_TIME = 'Mon Jan 24 2022 14:00:00 UTC';

export function checkTime(timeString = RELEASE_TIME) {
  let now = new Date().getTime();
  let deadline = new Date(timeString).getTime();
  return now >= deadline;
}

export const hexStringToStringText = (hexString) => {
  hexString = hexString.substring(2, hexString.length);
  if (hexString.length % 2 !== 0) {
    return '';
  } /* w w w.  jav  a2 s .  c o  m*/
  let numBytes = hexString.length / 2;
  let byteArray = new Uint8Array(numBytes);
  for (let i = 0; i < numBytes; i++) {
    byteArray[i] = parseInt(hexString.substr(i * 2, 2), 16);
  }
  return String.fromCharCode(...byteArray);
};
