import { useEffect, useState } from 'react';
import Header from './components/Header/Header';
import Banner from './components/Banner/Banner';
import Footer from './components/Footer/Footer';
import About from './views/About/About';
import Calendar from './views/Calendar/Calendar';
import { Routes, Route, Navigate } from 'react-router-dom';
import { EthereumContext, AccountContext, ChainContext } from './store/context';
import { Utils, CURRENT_SUPPORT_CHAIN_ID } from './utils/utils';
import detectEthereumProvider from '@metamask/detect-provider';
import { message } from 'antd';
import Web3 from 'web3';
import './App.scss';

function App() {
  const [ethereum] = useState(null);
  const [account, setAccount] = useState(null);
  const [chainId, setChainId] = useState(-1);

  useEffect(() => {
    readWeb3Instance().then();
    message.config({
      duration: 2,
      maxCount: 3,
    });
  }, []);

  async function readWeb3Instance() {
    const provider = await detectEthereumProvider();

    if (provider) {
      const newWeb3 = new Web3(provider);

      newWeb3.eth.net
        .getId()
        .then((res) => {
          setChainId(res);
          if (res === CURRENT_SUPPORT_CHAIN_ID) {
            Utils.setWeb3(provider);
            provider
              .request({ method: 'eth_requestAccounts' })
              .then((accounts) => {
                handleAccountsChanged(accounts).then();
              })
              .catch((err) => {
                console.error(err);
              });

            provider.on('accountsChanged', () => {
              window.location.reload();
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });

      provider.on('chainChanged', () => {
        window.location.reload();
      });
    } else {
      console.error('wait for MetaMask');
      Utils.setWeb3(false);
    }
  }

  const logout = () => {
    setAccount(null);
  };

  async function handleAccountsChanged(accounts) {
    if (accounts.length === 0) {
      // MetaMask is locked or the user has not connected any accounts
      console.log('Please connect to MetaMask.');
    } else if (accounts[0] !== account) {
      setAccount(accounts[0]);
    }
  }

  return (
    <EthereumContext.Provider value={ethereum}>
      <AccountContext.Provider value={account}>
        <ChainContext.Provider value={chainId}>
          <div className="App">
            <Header connect={readWeb3Instance} logout={logout} />
            <Banner />
            <div className="mainContent">
              <Routes>
                <Route path="/about" element={<About />} />
                <Route path="/calendar" element={<Calendar address={account} />} />
                <Route path="*" element={<Navigate to="/calendar" />} />
              </Routes>
            </div>
            <Footer />
          </div>
        </ChainContext.Provider>
      </AccountContext.Provider>
    </EthereumContext.Provider>
  );
}

export default App;
