import React, { Component, Fragment } from 'react';
import { Input, message } from 'antd';
import { CheckOutlined, CloseOutlined } from '@ant-design/icons';
import { addLeadingZero, numeral, COLORS, reduceMonth, reduceAddress } from '../../Shared';
import { Utils } from '../../utils/utils';
import { DEFAULT_PRICE } from '../../utils/config';
import './DayDetail.scss';

const { TextArea } = Input;
class DayDetail extends Component {
  constructor() {
    super();
    this.state = {
      buyLoading: false,
      buyActive: false,
      remark: '',
      nameInput: '',
      selectedColor: 1,
    };
  }
  componentDidUpdate = (prevProps) => {
    if (prevProps.date !== this.props.date) {
      this.setState({
        buyActive: false,
        buyLoading: false,
        remark: '',
        nameInput: '',
        selectedColor: 1,
      });
    }
  };
  getBuyDay = () => {
    return (
      <div className={'buyDay'}>
        <div className={'subtitle'}>Name your day</div>
        <Input className="nameInput" value={this.state.nameInput} onChange={this.onNameChange} />
        <div className={'subtitle'}>Choose Color</div>
        <div className={'colorSelectorWrapper'}>
          {COLORS.map((color, index) => {
            if (index > 0) {
              return (
                <div
                  className={
                    this.state.selectedColor === index ? 'colorSelector active' : 'colorSelector'
                  }
                  key={`color${index}`}
                  style={{ backgroundColor: color, boxShadow: `0 2px 10px 0 ${color}` }}
                  onClick={() => {
                    this.setState({
                      selectedColor: index,
                    });
                  }}
                >
                  {this.state.selectedColor === index ? (
                    <CheckOutlined style={{ color: 'white', fontWeight: 'bold' }} />
                  ) : null}
                </div>
              );
            } else {
              return null;
            }
          })}
        </div>
        <div className={'priceWrap'}>
          <div className={'subtitle'}>Price</div>
          <div className={'price'}>
            {numeral(this.props.info.price).format('0,0.[0000]')}{' '}
            <img src={`${process.env.PUBLIC_URL}/static/images/eth.png`} alt={'eth'} />
          </div>
        </div>
        <div className={'subtitle'}>Remark</div>
        <TextArea
          className={'textInput'}
          value={this.state.remark}
          onChange={this.onRemarkChange}
          placeholder="Input your day memo"
          autoSize={{ minRows: 5, maxRows: 20 }}
        />
        <div className={'button primary'} onClick={this.buy}>
          {this.props.info.owner ? 'Confirm Buy' : 'Mint'}
        </div>
      </div>
    );
  };

  getOwnedDay = () => {
    return (
      <div className={'ownedDay'}>
        <div className={'name'}>{this.props.info.name}</div>
        <div className={'subtitle'}>Owned By </div>
        <div className={'owner'}>
          {reduceAddress(this.props.info.owner)}
          <i
            style={{
              color: COLORS[this.props.info.color],
            }}
          >
            {this.props.info.owned ? '(You)' : ''}
          </i>
        </div>
        <div className={'subtitle'}>Color</div>
        <div
          className={'colorSelector'}
          style={{
            backgroundColor: COLORS[this.props.info.color],
            boxShadow: `0 2px 10px 0 ${COLORS[this.props.info.color]}`,
          }}
        />
        <div className={'priceWrap'}>
          <div className={'subtitle'}>Price</div>
          <div className={'price'}>
            {numeral(this.props.info.price).format('0,0.[0000]')}{' '}
            <img src={`${process.env.PUBLIC_URL}/static/images/eth.png`} alt={'eth'} />
          </div>
        </div>
        <div className={'subtitle'}>Remark</div>
        <div className={'remark'}>{this.props.info.remark}</div>
        {this.props.info.owned ? null : (
          <div
            className={'button primary'}
            onClick={() => {
              this.setState({ buyActive: true });
            }}
          >
            Buy Day
          </div>
        )}
      </div>
    );
  };

  onNameChange = (e) => {
    this.setState({ nameInput: e.target.value });
  };

  onRemarkChange = (e) => {
    this.setState({
      remark: e.target.value,
    });
  };
  render() {
    return (
      <div className="dayDetail" style={{ transform: this.props.transform }}>
        <div className={'monthDay'}>
          <Fragment>
            {' '}
            {reduceMonth(this.props.month)}. {addLeadingZero(this.props.date)}{' '}
          </Fragment>

          <CloseOutlined
            className={'closeButton'}
            onClick={() => {
              this.props.closeDetail();
              this.setState({
                buyActive: false,
              });
            }}
          />
        </div>
        <hr />
        {(this.props.info && !this.props.info.owner) || this.state.buyActive
          ? this.getBuyDay()
          : this.getOwnedDay()}
      </div>
    );
  }

  buy = () => {
    if (this.state.buyLoading) {
      return;
    }
    if (!this.props.address) {
      message.info('Please connect to wallet', 3);
      return;
    }
    if (parseFloat(this.props.myNativeBalance) < 0.01 + Number(this.props.info.price)) {
      message.info('Insufficient ETH', 3);
      return;
    }
    const web3Utils = Utils.web3.utils;
    const nameBytes = web3Utils.hexToBytes(web3Utils.utf8ToHex(this.state.nameInput));
    if (nameBytes.length > 100) {
      message.info('Name is too long', 2);
      return;
    }
    const remarkBytes = web3Utils.hexToBytes(web3Utils.utf8ToHex(this.state.remark));
    if (remarkBytes.length > 150) {
      message.info('Remark is too long', 2);
      return;
    }
    if (Utils.blockCalendar && Utils.blockCalendar.methods) {
      const args = [
        this.props.info.tokenId,
        web3Utils.utf8ToHex(this.state.nameInput),
        web3Utils.utf8ToHex(this.state.remark),
        this.state.selectedColor,
      ];
      console.log(args);
      Utils.blockCalendar.methods
        .buy(...args)
        .send({
          from: this.props.address,
          value: web3Utils.toWei(this.props.info.price.toString()),
        })
        .on('transactionHash', (hash) => {
          console.log(hash);
          this.setState({
            buyLoading: true,
          });
          message.info('Transaction sent', 3);
        })
        .then(() => {
          this.setState({
            buyLoading: false,
            buyActive: false,
            remark: '',
            nameInput: '',
            selectedColor: 1,
          });

          message.info('Transaction completed', 3);
          // this.props.getContractData();
        })
        .catch((err) => {
          console.log(1);
          console.log(err);
        });
    }
  };
}

export default DayDetail;
