import { Interface } from '@ethersproject/abi';
import Web3 from 'web3';
import { default as ADDRESSES } from './ADDRESSES.json';
import { default as BlockCalendar } from './abis/BlockCalendar.json';
import { default as Multicall } from './abis/Multicall.json';

const DEFAULT_RPC = 'https://rinkeby.infura.io/v3/ed6fbad307d14dc3ab11b986782eb710';
// const DEFAULT_RPC = 'https://mainnet.infura.io/v3/ed6fbad307d14dc3ab11b986782eb710';

export const CURRENT_SUPPORT_CHAIN_ID = 4;
export const Utils = {
  web3: false,
  blockCalendar: false,
  multicall: false,

  async setWeb3(provider) {
    if (!provider) {
      this.web3 = new Web3(DEFAULT_RPC);
      //       return;
    } else {
      this.web3 = new Web3(provider);
    }

    if (this.web3.utils.isAddress(ADDRESSES.multicall)) {
      this.multicall = new this.web3.eth.Contract(Multicall.abi, ADDRESSES.multicall);
    }

    if (this.web3.utils.isAddress(ADDRESSES.blockCalendar)) {
      this.blockCalendar = new this.web3.eth.Contract(BlockCalendar.abi, ADDRESSES.blockCalendar);
    }
  },
};

const PAGE = 100;
export const multicall = async (abi, calls) => {
  const multi = Utils.multicall;
  const itf = new Interface(abi);
  let res = [];

  if (calls.length > PAGE) {
    let i = 0;
    while (i < calls.length / PAGE) {
      const newCalls = calls.slice(i * PAGE, PAGE * (i + 1));
      const calldata = newCalls.map((call) => [
        call[0].toLowerCase(),
        itf.encodeFunctionData(call[1], call[2]),
      ]);
      const { returnData } = await multi.methods.aggregate(calldata).call();
      i++;
      res = res.concat(
        returnData.map((call, index) => itf.decodeFunctionResult(newCalls[index][1], call)),
      );
    }
  } else {
    const calldata = calls.map((call) => [
      call[0].toLowerCase(),
      itf.encodeFunctionData(call[1], call[2]),
    ]);
    const { returnData } = await multi.methods.aggregate(calldata).call();
    res = returnData.map((call, i) => itf.decodeFunctionResult(calls[i][1], call));
  }
  return res;
};
