import React, { Component } from 'react';
import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import Month from '../../components/Calendar/Month';
import { CALENDAR_CONFIG, DEFAULT_PRICE } from '../../utils/config';
import { Utils, multicall } from '../../utils/utils';
import { default as BlockCalendar } from '../../utils/abis/BlockCalendar.json';
import './Calendar.scss';
import { addLeadingZero, COLORS, hexStringToStringText } from '../../Shared';

class Calendar extends Component {
  constructor() {
    super();
    this.state = {
      myNativeBalance: 0,
      selectedMonth: 0,
      datesInfo: new Array(365).fill({
        tokenId: 0,
        price: DEFAULT_PRICE,
        priceInBN: DEFAULT_PRICE,
        name: 'Mint',
        color: 0,
        owner: '0x0000000000000000000000000000000000000000',
        remark: '',
        owned: false,
      }),
      startIndex: 0,
    };
  }

  componentDidMount = () => {
    this._mounted = true;
    this.checkContract();
    this.timer = setInterval(() => {
      this.checkContract();
    }, 1000);
  };

  componentWillUnmount = () => {
    this._mounted = false;
    clearTimeout(this.smeltTimeout);
  };

  checkContract = () => {
    if (Utils.web3 && Utils.blockCalendar) {
      clearInterval(this.timer);
      this.getContractData();
    }
  };

  getContractData = async () => {
    clearTimeout(this.smeltTimeout);
    try {
      if (!this._mounted) {
        return;
      }
      let myNativeBalance = 0;
      if (this.props.address) {
        myNativeBalance = Utils.web3.utils.fromWei(
          await Utils.web3.eth.getBalance(this.props.address),
        );
      }

      const tokenIds = [];
      CALENDAR_CONFIG.forEach((month, index) => {
        for (let i = 1; i <= month.max; i++) {
          tokenIds.push(`${index + 1}${addLeadingZero(i)}`);
        }
      });

      const calls = [];
      tokenIds.forEach((tokenId) => {
        calls.push([Utils.blockCalendar._address, 'tokenInfo', [tokenId]]);
      });
      const res = await multicall(BlockCalendar.abi, calls);
      const web3Utils = Utils.web3.utils;
      const startPrice = web3Utils.fromWei(await Utils.blockCalendar.methods.startPrice().call());
      const bidPricePerMillion = await Utils.blockCalendar.methods.bidPricePerMillion().call();
      const datesInfo = res.map((tokenInfo, index) => {
        let name = web3Utils.hexToUtf8(tokenInfo.name);
        let price = web3Utils.fromWei(tokenInfo.lastPrice.toString());
        let remark = web3Utils.hexToUtf8(tokenInfo.blog);
        if (Number(price) >= Number(startPrice)) {
          price = web3Utils.fromWei(
            web3Utils
              .toBN(web3Utils.toWei(price))
              .mul(web3Utils.toBN(bidPricePerMillion))
              .div(web3Utils.toBN(1000000)),
          );
          if (name === '') {
            name = '--';
          }
          if (remark === '') {
            remark = '--';
          }
        } else {
          price = startPrice;
        }

        const skin = Number(tokenInfo.skin.toString());
        return {
          tokenId: tokenIds[index],
          price: price,
          color: skin,
          name: name,
          remark: remark,
        };
      });

      const mintedDatesInfo = datesInfo.reduce((prevDate, currDate, index) => {
        if (Number(currDate.price) > Number(startPrice)) {
          currDate.index = index;
          prevDate.push(currDate);
        }
        return prevDate;
      }, []);
      const ownerCalls = [];
      mintedDatesInfo.forEach((item) => {
        ownerCalls.push([Utils.blockCalendar._address, 'ownerOf', [item.tokenId]]);
      });
      const ownerRes = await multicall(BlockCalendar.abi, ownerCalls);
      mintedDatesInfo.forEach((item, index) => {
        datesInfo[item.index].owner = ownerRes[index][0];
        datesInfo[item.index].owned =
          this.props.address &&
          ownerRes[index][0].toLowerCase() === this.props.address.toLowerCase();
      });

      this.calcStartIndex();

      if (!this._mounted) {
        return;
      }
      this.setState({
        datesInfo: datesInfo,
        myNativeBalance: myNativeBalance,
      });
    } catch (e) {
      console.log(e);
    }

    this.smeltTimeout = setTimeout(() => {
      this.getContractData();
    }, 10000);
  };

  calcStartIndex = () => {
    let startIndex = 0;
    for (let j = 0; j < this.state.selectedMonth; j++) {
      startIndex = startIndex + CALENDAR_CONFIG[this.state.selectedMonth - 1].max;
    }

    this.setState({
      startIndex: startIndex,
    });
  };

  selectMonth = (month) => {
    this.setState({ selectedMonth: month }, () => {
      this.calcStartIndex();
    });
  };
  render() {
    return (
      <div className={'container'}>
        <div className="calendar">
          <div className={'calendarHeader'}>
            <div
              className={'icon'}
              onClick={() => {
                this.selectMonth((this.state.selectedMonth + 11) % 12);
              }}
            >
              <LeftOutlined />
            </div>
            <div className={'monthName'}>{CALENDAR_CONFIG[this.state.selectedMonth].month}</div>
            <div className={'icon'}>
              <RightOutlined
                onClick={() => {
                  this.selectMonth((this.state.selectedMonth + 1) % 12);
                }}
              />
            </div>
          </div>
          <div className={'contentWrapper'}>
            <div className={'monthList'}>
              {CALENDAR_CONFIG.map((item, index) => (
                <div
                  key={`month${index}`}
                  className={
                    this.state.selectedMonth === index ? 'monthButton active' : 'monthButton'
                  }
                  onClick={() => {
                    this.selectMonth(index);
                  }}
                >
                  {CALENDAR_CONFIG[index].month}
                </div>
              ))}
            </div>

            <Month
              data={CALENDAR_CONFIG[this.state.selectedMonth]}
              month={this.state.selectedMonth}
              datesInfo={this.state.datesInfo.slice(
                this.state.startIndex,
                this.state.startIndex + CALENDAR_CONFIG[this.state.selectedMonth].max,
              )}
              getContractData={this.getContractData}
              myNativeBalance={this.state.myNativeBalance}
              address={this.props.address}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Calendar;
