import React, { Component } from 'react';
import './styles.scss';
/**
 * Note :
 * If you're using react v 15.4 or less
 * You can directly import PropTypes from react instead.
 * Refer to this : https://reactjs.org/warnings/dont-call-proptypes.html
 */
class Countdown extends Component {
  constructor(props) {
    super(props);

    this.state = {
      days: 0,
      hours: 0,
      min: 0,
      sec: 0,
    };
  }

  componentDidMount() {
    // update every second
    this.interval = setInterval(() => {
      const date = this.calculateCountdown(this.props.date);
      date ? this.setState(date) : this.stop();
    }, 1000);
  }

  componentDidUpdate = (prevProps) => {
    if (this.props.date !== prevProps.date) {
      this.interval = setInterval(() => {
        const date = this.calculateCountdown(this.props.date);
        date ? this.setState(date) : this.stop();
      }, 1000);
    }
  };

  componentWillUnmount() {
    this.stop();
  }

  calculateCountdown(endDate) {
    let diff;
    diff = (Date.parse(new Date(endDate).toString()) - Date.parse(new Date().toString())) / 1000;

    // clear countdown when date is reached
    if (diff <= 0) return false;

    const timeLeft = {
      years: 0,
      days: 0,
      hours: 0,
      min: 0,
      sec: 0,
    };

    // calculate time difference between now and expected date
    if (diff >= 365.25 * 86400) {
      // 365.25 * 24 * 60 * 60
      timeLeft.years = Math.floor(diff / (365.25 * 86400));
      diff -= timeLeft.years * 365.25 * 86400;
    }
    if (diff >= 86400) {
      // 24 * 60 * 60
      timeLeft.days = Math.floor(diff / 86400);
      diff -= timeLeft.days * 86400;
    }
    if (diff >= 3600) {
      // 60 * 60
      timeLeft.hours = Math.floor(diff / 3600);
      diff -= timeLeft.hours * 3600;
    }
    if (diff >= 60) {
      timeLeft.min = Math.floor(diff / 60);
      diff -= timeLeft.min * 60;
    }
    timeLeft.sec = diff;

    return timeLeft;
  }

  stop() {
    clearInterval(this.interval);
  }

  addLeadingZeros(value) {
    value = String(value);
    while (value.length < 2) {
      value = '0' + value;
    }
    return value;
  }

  render() {
    const countDown = this.state;
    return (
      <span className="Countdown" align="middle">
        {parseInt(countDown.days) > 0 ? (
          <span className="Countdown-col">
            <span className="Countdown-col-element">
              <strong>{this.addLeadingZeros(countDown.days)}&nbsp;:&nbsp;</strong>
            </span>
          </span>
        ) : null}

        <span className="Countdown-col">
          <span className="Countdown-col-element">
            <strong>{this.addLeadingZeros(countDown.hours)}&nbsp;:&nbsp;</strong>
          </span>
        </span>

        <span className="Countdown-col">
          <span className="Countdown-col-element">
            <strong>{this.addLeadingZeros(countDown.min)}&nbsp;:&nbsp;</strong>
          </span>
        </span>
        <span className="Countdown-col">
          <span className="Countdown-col-element">
            <strong> {this.addLeadingZeros(countDown.sec)}</strong>
          </span>
        </span>
      </span>
    );
  }
}

Countdown.defaultProps = {
  date: new Date().toString(),
};

export default Countdown;
