import { useContext, useState } from 'react';
import { EthereumContext, AccountContext, ChainContext } from '../../store/context';
import { Link, NavLink } from 'react-router-dom';
import { MenuOutlined } from '@ant-design/icons';
import './Header.scss';

function Header(props) {
  const [showMenu, setShowMenu] = useState(false);
  const account = useContext(AccountContext);

  const toggleMenu = () => {
    setShowMenu(!showMenu);
  };
  return (
    <div className="header">
      <div className="container">
        <Link className="logoWrap" to="/">
          <img
            className="logoImg"
            src={`${process.env.PUBLIC_URL}/static/images/logo.png`}
            alt="logo"
          />
          <p className="logoText">BLOCK CALENDAR</p>
        </Link>
        <div className={`menuWrap ${showMenu ? 'show' : ''}`}>
          <div className="navWrap">
            <NavLink
              className="button"
              to="/calendar"
              onClick={() => {
                setShowMenu(false);
              }}
            >
              CALENDAR
            </NavLink>
            <NavLink
              className="button"
              to="/about"
              onClick={() => {
                setShowMenu(false);
              }}
            >
              FAQ
            </NavLink>
          </div>
          {account ? (
            <div className="logout button" onClick={props.logout}>
              Logout
            </div>
          ) : (
            <div
              className="connect button primary"
              onClick={() => {
                props.connect();
                setShowMenu(false);
              }}
            >
              CONNECT WALLET
            </div>
          )}
        </div>
        <div className="menuIcon" onClick={toggleMenu}>
          <MenuOutlined />
        </div>
      </div>
    </div>
  );
}

export default Header;
